.composition-box {
  display: grid;
  width: 100%;
  grid-template-columns: repeat(3, 1fr);
  font-family: "Inter"; }
  @media only screen and (max-width: 25em) {
    .composition-box {
      grid-template-columns: 100%;
      grid-template-rows: repeat(3, max-content); } }
  .composition-box__container {
    display: flex;
    flex-flow: column; }
    @media only screen and (max-width: 25em) {
      .composition-box__container {
        margin-bottom: 4rem; } }
  .composition-box__number {
    font-weight: 700;
    font-size: 2.4rem;
    margin-bottom: 0.5rem; }
  .composition-box__name {
    font-size: 1.1rem;
    letter-spacing: 1px;
    text-transform: uppercase;
    color: rgba(255, 255, 255, 0.75); }

.img-box {
